.section-about .container {
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
}

.section-about h2.title {
  text-align: left;
  letter-spacing: 1.2px;
  margin-bottom: 56px;
}

.section-about h2.title::after {
  left: 0;
}

.about-photo-wrap {
  position: absolute;
  left: 0;
  width: 42vw;
  height: 100%;
  overflow: hidden;
  justify-content: flex-end;
  background: url("../img/about-img.jpg") no-repeat 65%;
  background-size: cover;
  display: none;
  /*	img {
				width: auto;
				height: 100%;
				max-width: none;
			}*/
}

@media only screen and (min-width: 768px) {
  .about-photo-wrap {
    display: flex;
  }
}

.about-text-wrap {
  max-width: 670px;
  padding-top: 8px;
  padding-left: 10px;
}

.about-text__text {
  font-size: 16px;
  color: #2b2a29;
  padding-left: 3px;
  line-height: 1.6;
  margin-bottom: 24px;
}

@media only screen and (min-width: 768px) {
  .about-text__text {
    font-size: 15px;
  }
}

.section-contacts {
  padding-top: 35px;
  padding-bottom: 31px;
}

@media only screen and (min-width: 768px) {
  .section-contacts {
    padding-top: 65px;
  }
}

.section-contacts .font-icon {
  margin-right: 15px;
}

.section-contacts .font-icon::before {
  font-size: 39px;
  color: #389cab;
}

.section-contacts h2.title {
  margin-bottom: 74px;
}

.contacts-group {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}

@media only screen and (min-width: 768px) {
  .contacts-group {
    flex-direction: row;
  }
}

.contacts-group__item {
  display: flex;
  max-width: 100%;
  width: 100%;
  height: 100px;
}

@media only screen and (min-width: 768px) {
  .contacts-group__item {
    max-width: 21%;
    width: auto;
  }
}

.contacts-info__name {
  font-size: 14px;
  color: #818180;
  margin-bottom: 10px;
}

.contacts-info__text {
  font-size: 16px;
  font-weight: 700;
  color: #2b2a29;
  line-height: 1.6;
}

.section-map {
  height: 400px;
}

.section-popup-thanks {
  position: fixed;
  z-index: 30;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  display: none;
}

.section-popup-thanks h2.title {
  color: #fff;
}

.section-popup-thanks h2.title::after {
  display: none;
}

.opened-popup-thanks .section-popup-thanks {
  display: block;
}

.popup-wrap {
  display: flex;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  overflow: auto;
}

.popup-overlaw {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  background: #000;
  opacity: 0.5;
  cursor: pointer;
}

.popup-overlaw:hover {
  opacity: 0.4;
}

@keyframes fadeInDown {
  from {
    transform: translateY(-15px);
    opacity: 0;
  }
  to {
    transform: none;
    opacity: 1;
  }
}

.popup-content {
  position: relative;
  max-width: 470px;
  margin: 0 auto;
  animation: fadeInDown .3s ease;
  animation-fill-mode: both;
  background: linear-gradient(to right, #369ca9, #57c7a6);
  padding: 30px 0 30px 0;
  margin-top: 180px;
  width: 320px;
  text-align: center;
  border: 5px solid #fff;
  border-radius: 10px;
}

.popup-content::before {
  content: '';
  background: url(../img/popup-thanks-bg.png) no-repeat center;
  background-size: contain;
  width: 54px;
  height: 54px;
  display: block;
  margin: 0 auto 30px;
}

@media only screen and (min-width: 768px) {
  .popup-content {
    width: 470px;
  }
}

.popup-close-icon {
  position: absolute;
  background: url(../img/popup-close-icon.png) no-repeat center;
  right: -3px;
  top: -50px;
  background-size: 15px 15px;
  width: 15px;
  height: 15px;
  display: block;
  border: none;
}

@media only screen and (min-width: 768px) {
  .popup-close-icon {
    right: -43px;
    top: -50px;
  }
}

.popup-form-title {
  font-size: 22px;
  font-weight: 700;
  color: #fff;
  text-transform: uppercase;
  margin-top: 20px;
  margin-bottom: 5px;
}

@media only screen and (min-width: 768px) {
  .popup-form-title {
    font-size: 26px;
    margin-top: 50px;
    margin-bottom: 10px;
  }
}

.popup-form-text {
  font-size: 15px;
  line-height: 1.4;
  color: #fff;
  padding: 0 20px;
}
